<template>
  <div class="help-details-page full-screen">
    <div class="page-panel">
      <pem-scroll-view 
        class="scroll-view"
        ref="scroll" 
        :pullup=true
        @pullUpLoad="resizeWrapper">
        <div class="wrap" v-show="showPage">
            <div class="help-title" v-html="title"></div>
            <div class="help-content" v-html="content"></div>
        </div>
    </pem-scroll-view>
    </div>
    <pem-toast ref="toast" :text="toastMsg"></pem-toast>
  </div>
</template>

<script type="text/ecmascript-6">
import PemScrollView from 'components/scroll-view/scroll-view'
import PemToast from 'components/pem-toast/pem-toast'
import { MiniSiteApi } from 'api'
import { setTitle } from 'common/js/util'

export default {
  data () {
    return {
      showPage: false,
      title: '',
      content: '',
      toastMsg: ''
    }
  },
  components: {
    PemScrollView,
    PemToast
  },
  created () {
    let helpId = this.$route.params.id.toString()
    this.getDetails(helpId)
    this._setTitle('帮助中心')
  },
  mounted () {
    this.$nextTick(function () {
      this.showPage = true
    })
  },
  methods: {
    _setTitle (title) {
      setTitle(title)
    },
    getDetails (id) {
      MiniSiteApi.getHelpDetail({id}).then(res => {
        if (+res.resultcode === 1) {
          let result = res.resultList ? res.resultList : {}
          this.title = result.title
          this.content = result.content || ''
          if (this.content.indexOf('<img') > -1) {
            setTimeout(() => {
              this.resizeWrapper()
            }, 1000)
          }
        } else if (+res.resultcode === 0) {
          this.toastMsg = '系统错误'
          this.$refs.toast.show()
        } else if (+res.resultcode === -11) {
          this.toastMsg = 'id 为空'
          this.$refs.toast.show()
        }
      })
    },
    resizeWrapper () {
      this.$refs.scroll.refresh()
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
 .help-details-page >>> b,strong
    font-weight: bold
 .page-panel
    top: 0
 .wrap
    padding: 0 0.3rem 1rem 0.3rem
    line-height: 22px
    background-color: #ffffff
    .help-title
      font-size: 16px
      font-weight: bold
      color: #41403E
      padding: 10px 0
    .help-content, p
      font-size: 15px
      word-break: break-all

</style>



